import Users from './index';
import {connect} from "react-redux";
import {useEffect} from "react";
import {compose} from "redux";
import {updateSearchData, toggleFollow, fetchUsers} from '../../redux/users/reducer';
import {getFollowingInProgress, getFriends, getLoading, getSearchData, getUsers} from "../../redux/users/selectors";
import {objectToQueryString} from "../../utils/helpers/object";

const UsersAPIContainer = props => {
    let {
        searchData,
        fetchUsers,
        updateSearchData,
        location: {search},
        ...other
    } = props;

    const {page, count, term} = searchData;

    useEffect(()=> {
        const searchPage = Number(new URLSearchParams(search).get('page')) || 1;
        const searchQuery = new URLSearchParams(search).get('term') || '';

        if(Number(searchPage) !== page || searchQuery !== term) {
            updateSearchData({
                term: searchQuery,
                page: searchPage
            })
        }
        else {
            fetchUsers({page, count, term});
        }

    }, [page, count, term, search, updateSearchData, fetchUsers]);

    const setPage = page => {
        props.history.push('/users?'+objectToQueryString({page, count, term}));
    };
    const runSearch = term => {
        props.history.push('/users?' + objectToQueryString({page: 1, count, term}));
    };

    return Users({searchData, setPage, runSearch, ...other});
};

export default compose(
    connect(
        state => ({
            loading: getLoading(state),
            users: getUsers(state),
            friends: getFriends(state),
            searchData: getSearchData(state),
            followingInProgress: getFollowingInProgress(state),
            myId: state.authData.id,
            isAuth: state.authData.isAuth
        }),
        {
            updateSearchData,
            toggleFollow,
            fetchUsers
        }
    )
)(UsersAPIContainer)

