import React from 'react';
import s from './Users.module.scss';
import ListItem from './ListItem'
import Pagination from '../common/Pagination'
import Preloader from '../common/Preloader'
import Search from './Search';
import Icon from '../common/Icon';

export default ({
    loading,
    users,
    searchData,
    setPage,
    runSearch,
    ...other
}) => (
    <div className={s.Users} key={'Users'}>
        <div className={s.title}>Users</div>

        <Search term={searchData.term} runSearch={runSearch}/>

        {(!users.length && searchData.term !== '') &&
            <div className={s.NotFound}>
                <Icon icon={'alert'} className={s.NotFoundIcon}/>
                Не найдены
            </div>
        }

        <Preloader loading={loading}/>

        <ul className={s.list + (loading ? ' ' + s.loading: '')}>
            {users.map(user => ListItem({user, ...other}))}
        </ul>

        {users.length > 0 &&
            <Pagination
                page={searchData.page}
                total={searchData.total}
                onClickHandler={setPage}
            />
        }

    </div>
);

