import React from 'react';
import {LIMIT} from '../../../redux/constants';
import s from "./Pagination.module.scss";

export default ({page, total, onClickHandler}) => {
    const width = 2;
    const last = Math.ceil(total / LIMIT);
    const left = page - width;
    const right = page + width + 1;
    const range = [];
    const rangeWithDots = [];
    let l;

    for (let i = 1; i <= last; i += 1) {
        if (i === 1 || i === last || (i >= left && i <= right)) {
            range.push(i);
        } else if (i < left) {
            i = left - 1;
        } else if (i > right) {
            range.push(last);
            break;
        }
    }

    range.forEach(i => {
        if (l) {
            if (i - l === 2) {
                rangeWithDots.push(l + 1);
            } else if (i - l !== 1) {
                rangeWithDots.push('...');
            }
        }
        rangeWithDots.push(i);
        l = i;
    });

    return (
        <div className={s.Pagination}>
            <button
                key={'prev'}
                className={page === 1 ? s.Item + ' ' + s.Disabled: s.Item}
                disabled={page === 1 ? 'disabled': ''}
                onClick={ () => onClickHandler(page - 1) }
            >
                {'<'}
            </button>

            {rangeWithDots.map((p, i) => {
                if(!isNaN(p)) {
                    return (
                        <button
                            key={p+i}
                            className={p === page ? s.Item + ' ' + s.Active: s.Item}
                            disabled={p === page ? 'disabled': ''}
                            onClick={ () => onClickHandler(p) }
                        >
                            {p}
                        </button>
                    )
                }
                else {
                    return <div key={p+i} className={s.Dots}>...</div>
                }
            })}

            <button
                key={'next'}
                className={page === last ? s.Item + ' ' + s.Disabled: s.Item}
                disabled={page === last ? 'disabled': ''}
                onClick={ () => onClickHandler(page + 1) }
            >
                {'>'}
            </button>
        </div>
    );
}
