import React, {useEffect, useState} from 'react';
import s from './Search.module.scss';
import Icon from '../../common/Icon';

export default ({term, runSearch}) => {
    const [value, setValue] = useState(term);
    const [active, setActive] = useState(term !== '');

    useEffect(()=> {
        setValue(term);
        setActive(term !== '');
    }, [term]);

    const onChange = e => {
        const v = e.target.value;
        setValue(v);

        if(!v.length || v.length > 2) runSearch(v);
        setActive(v.length > 2);
    };
    return (
        <div className={s.Search}>
            <input type="text" className={s.Field + (active ? ' ' + s.Active: '')} value={value} onChange={onChange}/>
            {active
                ? <button className={s.Btn} onClick={() => runSearch('')}>
                    <Icon className={s.Icon + ' ' + s.IconClose} icon={'close'}/>
                </button>
                : <button className={s.Btn}>
                    <Icon className={s.Icon + ' ' + s.IconSearch} icon={'search'}/>
                </button>
            }
        </div>
    );
}
