import React from 'react';
import s from "./Users.module.scss";
import {Link} from "react-router-dom";
import Icon from '../common/Icon'
import defaultAva from '../../assets/images/logo192.png'

export default props => {
    const {
        myId,
        isAuth,
        followingInProgress,
        toggleFollow,
        user: {
            id,
            name,
            followed,
            status,
            photos: {
                small
            },
        }
    } = props;

    const showButton = !(id === myId || !isAuth);

    return (
        <li className={s.item} key={id}>
            <div className={s.avaWrapper}>
                <Link to={'/profile/' + id} className={s.avaLink}>
                    <img className={s.ava} src={small ? small: defaultAva} alt={name}/>
                </Link>
                {showButton &&
                    <button
                        className={s.btn + ' ' + (followed ? s.unfollow : s.follow)}
                        onClick={() => toggleFollow(id, followed)}
                        disabled={followingInProgress.some(fid => fid === id)}
                    >
                        {followed ? 'Unfollow' : 'Follow'}
                    </button>
                }
            </div>
            <div className={s.UserName}>
                {status && (
                    <div className={s.Status}>
                        <span>{status}</span>
                        <Icon icon={'quotes'} className={s.StatusIcon}/>
                    </div>
                )}
                <h3>
                    {name}
                </h3>
            </div>
        </li>
    );
}
